 15px -3px rgba(0, 0, 0, 0.1);\n  --khipu-shadow-lg: 0 20px 25px -5px rgba(0, 0, 0, 0.1);\n}\n\n// Reset and base\n* {\n  box-sizing: border-box;\n}\n\n// Main container\n.khipu-chat-widget {\n  position: fixed;\n  z-index: 999999;\n  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', Arial, sans-serif;\n  \n  * {\n    box-sizing: border-box;\n  }\n}\n\n// Floating button\n.khipu-chat-button {\n  width: 60px;\n  height: 60px;\n  border-radius: 50%;\n  background: var(--primary-color, var(--khipu-primary));\n  border: none;\n  box-shadow: var(--khipu-shadow-lg);\n  cursor: pointer;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  transition: all 0.3s ease;\n  position: relative;\n  \n  &:hover {\n    transform: scale(1.1);\n    background: var(--khipu-primary-dark);\n  }\n  \n  &:active {\n    transform: scale(0.95);\n  }\n  \n  &.khipu-chat-button-hidden {\n    opacity: 0;\n    visibility: hidden;\n    transform: scale(0);\n  }\n  \n  .khipu-chat-icon {\n    width: 28px;\n    height: 28px;\n    color: white;\n  }\n  \n  .khipu-chat-badge {\n    position: absolute;\n    top: -5px;\n    right: -5px;\n    background: #EF4444;\n    color: white;\n    font-size: 11px;\n    font-weight: 600;\n    min-width: 20px;\n    height: 20px;\n    border-radius: 10px;\n    display: none;\n    align-items: center;\n    justify-content: center;\n    padding: 0 5px;\n  }\n}\n\n// Chat window\n.khipu-chat-window {\n  position: fixed;\n  width: 380px;\n  max-width: calc(100vw - 40px);\n  height: 600px;\n  max-height: calc(100vh - 100px);\n  background: var(--khipu-bg);\n  border-radius: 16px;\n  box-shadow: var(--khipu-shadow-lg);\n  display: flex;\n  flex-direction: column;\n  overflow: hidden;\n  animation: slideUp 0.3s ease;\n  \n  @keyframes slideUp {\n    from {\n      opacity: 0;\n      transform: translateY(20px);\n    }\n    to {\n      opacity: 1;\n      transform: translateY(0);\n    }\n  }\n  \n  @media (max-width: 480px) {\n    width: 100vw;\n    height: 100vh;\n    max-width: 100vw;\n    max-height: 100vh;\n    border-radius: 0;\n    bottom: 0 !important;\n    right: 0 !important;\n    left: 0 !important;\n    top: 0 !important;\n  }\n}\n\n// Header\n.khipu-chat-header {\n  background: var(--primary-color, var(--khipu-primary));\n  color: white;\n  padding: 16px 20px;\n  display: flex;\n  align-items: center;\n  justify-content: space-between;\n  flex-shrink: 0;\n  \n  .khipu-chat-header-info {\n    display: flex;\n    align-items: center;\n    gap: 12px;\n    flex: 1;\n  }\n  \n  .khipu-chat-avatar {\n    width: 40px;\n    height: 40px;\n    border-radius: 50%;\n    background: rgba(255, 255, 255, 0.2);\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    font-size: 20px;\n    flex-shrink: 0;\n    \n    img {\n      width: 100%;\n      height: 100%;\n      border-radius: 50%;\n      object-fit: cover;\n    }\n  }\n  \n  .khipu-chat-header-text {\n    flex: 1;\n    min-width: 0;\n  }\n  \n  .khipu-chat-title {\n    font-weight: 600;\n    font-size: 16px;\n    margin: 0;\n    white-space: nowrap;\n    overflow: hidden;\n    text-overflow: ellipsis;\n  }\n  \n  .khipu-chat-status {\n    font-size: 13px;\n    opacity: 0.9;\n    margin-top: 2px;\n  }\n  \n  .khipu-chat-header-actions {\n    display: flex;\n    gap: 8px;\n    \n    button {\n      width: 32px;\n      height: 32px;\n      border: none;\n      background: rgba(255, 255, 255, 0.2);\n      border-radius: 6px;\n      cursor: pointer;\n      display: flex;\n      align-items: center;\n      justify-content: center;\n      transition: background 0.2s;\n      \n      &:hover {\n        background: rgba(255, 255, 255, 0.3);\n      }\n      \n      svg {\n        width: 18px;\n        height: 18px;\n        color: white;\n      }\n    }\n  }\n}\n\n// Messages container\n.khipu-chat-messages {\n  flex: 1;\n  overflow-y: auto;\n  padding: 20px;\n  display: flex;\n  flex-direction: column;\n  gap: 16px;\n  \n  &::-webkit-scrollbar {\n    width: 6px;\n  }\n  \n  &::-webkit-scrollbar-track {\n    background: transparent;\n  }\n  \n  &::-webkit-scrollbar-thumb {\n    background: var(--khipu-border);\n    border-radius: 3px;\n    \n    &:hover {\n      background: var(--khipu-text-light);\n    }\n  }\n}\n\n// Message\n.khipu-chat-message {\n  display: flex;\n  animation: fadeIn 0.3s ease;\n  \n  @keyframes fadeIn {\n    from {\n      opacity: 0;\n      transform: translateY(10px);\n    }\n    to {\n      opacity: 1;\n      transform: translateY(0);\n    }\n  }\n  \n  &.khipu-chat-message-user {\n    justify-content: flex-end;\n    \n    .khipu-chat-message-content {\n      background: var(--primary-color, var(--khipu-primary));\n      color: white;\n      border-radius: 18px 18px 4px 18px;\n    }\n  }\n  \n  &.khipu-chat-message-bot {\n    justify-content: flex-start;\n    \n    .khipu-chat-message-content {\n      background: #F3F4F6;\n      color: var(--khipu-text);\n      border-radius: 18px 18px 18px 4px;\n    }\n  }\n}\n\n.khipu-chat-message-content {\n  max-width: 75%;\n  padding: 12px 16px;\n  word-wrap: break-word;\n  \n  @media (max-width: 480px) {\n    max-width: 85%;\n  }\n}\n\n.khipu-chat-message-text {\n  font-size: 14px;\n  line-height: 1.5;\n  \n  p {\n    margin: 0 0 8px 0;\n    \n    &:last-child {\n      margin-bottom: 0;\n    }\n  }\n  \n  a {\n    color: inherit;\n    text-decoration: underline;\n  }\n  \n  code {\n    background: rgba(0, 0, 0, 0.1);\n    padding: 2px 6px;\n    border-radius: 4px;\n    font-family: monospace;\n    font-size: 13px;\n  }\n}\n\n.khipu-chat-message-time {\n  font-size: 11px;\n  margin-top: 4px;\n  opacity: 0.7;\n}\n\n// Quick replies\n.khipu-chat-quick-replies {\n  display: flex;\n  flex-wrap: wrap;\n  gap: 8px;\n  margin-top: 8px;\n}\n\n.khipu-chat-quick-reply {\n  background: white;\n  border: 1px solid var(--primary-color, var(--khipu-primary));\n  color: var(--primary-color, var(--khipu-primary));\n  padding: 8px 16px;\n  border-radius: 20px;\n  font-size: 13px;\n  cursor: pointer;\n  transition: all 0.2s;\n  \n  &:hover {\n    background: var(--primary-color, var(--khipu-primary));\n    color: white;\n  }\n}\n\n// Typing indicator\n.khipu-chat-typing {\n  display: flex;\n  gap: 4px;\n  padding: 12px 16px;\n  background: #F3F4F6;\n  border-radius: 18px;\n  width: fit-content;\n  \n  .khipu-chat-typing-dot {\n    width: 8px;\n    height: 8px;\n    border-radius: 50%;\n    background: var(--khipu-text-light);\n    animation: typing 1.4s infinite;\n    \n    &:nth-child(2) {\n      animation-delay: 0.2s;\n    }\n    \n    &:nth-child(3) {\n      animation-delay: 0.4s;\n    }\n  }\n  \n  @keyframes typing {\n    0%, 60%, 100% {\n      transform: translateY(0);\n      opacity: 0.7;\n    }\n    30% {\n      transform: translateY(-10px);\n      opacity: 1;\n    }\n  }\n}\n\n// Input container\n.khipu-chat-input-container {\n  padding: 16px 20px;\n  border-top: 1px solid var(--khipu-border);\n  display: flex;\n  gap: 12px;\n  align-items: flex-end;\n  flex-shrink: 0;\n  background: var(--khipu-bg);\n}\n\n.khipu-chat-input {\n  flex: 1;\n  border: 1px solid var(--khipu-border);\n  border-radius: 20px;\n  padding: 10px 16px;\n  font-size: 14px;\n  font-family: inherit;\n  resize: none;\n  outline: none;\n  min-height: 40px;\n  max-height: 100px;\n  transition: border-color 0.2s;\n  \n  &:focus {\n    border-color: var(--primary-color, var(--khipu-primary));\n  }\n  \n  &::placeholder {\n    color: var(--khipu-text-light);\n  }\n}\n\n.khipu-chat-send {\n  width: 40px;\n  height: 40px;\n  border: none;\n  background: var(--primary-color, var(--khipu-primary));\n  border-radius: 50%;\n  cursor: pointer;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  flex-shrink: 0;\n  transition: all 0.2s;\n  \n  &:hover {\n    background: var(--khipu-primary-dark);\n    transform: scale(1.05);\n  }\n  \n  &:active {\n    transform: scale(0.95);\n  }\n  \n  svg {\n    width: 20px;\n    height: 20px;\n    color: white;\n  }\n}\n"],"sourceRoot":""}]);
